.faq {
  border-bottom: 1px solid #dee6eb;
}
.faq:first-child .faq__title {
  padding-top: 0;
}
.faq__container {
  position: relative;
}
.faq__top {
  align-items: center;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
}
.faq__top:hover .faq__arrow img {
  filter: invert(100%);
}
.faq__top:hover .faq__arrow:after {
  transform: none;
}
.faq__arrow {
  align-items: center;
  cursor: pointer;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  height: 4rem;
  width: 4rem;
  position: relative;
  transition: all 0.4s cubic-bezier(0.4, 0, 0, 1);
}
.faq__arrow img {
  position: relative;
  z-index: 10;
}
.faq__arrow:before,
.faq__arrow:after {
  background-color: #eee;
  border-radius: 50%;
  content: '';
  left: 0;
  position: absolute;
  height: 4rem;
  width: 4rem;
  top: 0;
  transition: all 0.4s cubic-bezier(0.4, 0, 0, 1);
  z-index: -1;
}
.faq__arrow:after {
  background-color: #e2e2e2;
  height: 4.8rem;
  width: 4.8rem;
  transform: scale(0);
  left: -0.4rem;
  top: -0.4rem;
}
.faq__arrow:hover:after {
  transform: none;
}
.faq__arrow:after {
  background-color: #005648;
}
.faq__arrow img {
  transition: all 0.4s cubic-bezier(0.4, 0, 0, 1);
}
.faq__title {
  font-size: 2.4rem;
  line-height: 1.2;
  padding: 1em 0;
  padding-right: 1.5em;
}
.faq__text {
  display: none;
  color: #393d41;
  padding-right: 12rem;
}
.faq__text p {
  color: #393d41;
}
.faq_active .faq__arrow {
  transform: rotate(180deg);
}
.faq_active .faq__arrow:before {
  background-color: #868b8f;
}
.faq_active .faq__arrow img {
  filter: invert(100%);
}
.prems {
