.news .new {
  align-items: center;
  display: flex;
  justify-content: space-between;
  opacity: 0;
  padding: 3.2rem 0;
  position: relative;
  transform: translateY(4rem);
  transition: all 1s cubic-bezier(0.4, 0, 0, 1);
}
.news .new:before {
  background-color: #e61830;
  bottom: 0;
  content: '';
  height: 0;
  left: 0;
  position: absolute;
  transition: height cubic-bezier(0.4, 0, 0, 1) 0.4s;
  width: 100%;
  z-index: -1;
}
.news .new:hover:before {
  top: 0;
  height: 100%;
}
.news .new:nth-child(1) {
  transition-delay: 0s;
}
.news .new:nth-child(2) {
  transition-delay: 0.1s;
}
.news .new:nth-child(3) {
  transition-delay: 0.2s;
}
.news .new:nth-child(4) {
  transition-delay: 0.3s;
}
.news .new:nth-child(5) {
  transition-delay: 0.4s;
}
.news .new:nth-child(6) {
  transition-delay: 0.5s;
}
.news .new:nth-child(7) {
  transition-delay: 0.6s;
}
.news .new:nth-child(8) {
  transition-delay: 0.7s;
}
.news .new:nth-child(9) {
  transition-delay: 0.8s;
}
.news .new:nth-child(10) {
  transition-delay: 0.9s;
}
.news .new:before {
  left: -10%;
  width: 120%;
}
.news .new:not(:last-child) {
  border-bottom: 1px solid #dee6eb;
}
.news .new__title {
  font-size: 2.4rem;
  font-weight: 600;
  margin-bottom: 0.5em;
  transition: all 0.4s cubic-bezier(0.4, 0, 0, 1);
}
.news .new__date {
  transition: all 0.4s cubic-bezier(0.4, 0, 0, 1);
}
.news .new__os {
  display: flex;
  justify-content: center;
  width: 4rem;
}
.news .new__os img {
  transition: all 0.4s cubic-bezier(0.4, 0, 0, 1);
}
.news .new:hover .new__title {
  color: #fff;
}
.news .new:hover .new__date {
  color: #fff;
}
.news .new:hover .new__os img {
  filter: contrast(3);
}
.news.animated .new {
  opacity: 1;
  transform: none;
}
@media (max-width: 767px) {
  .news .new {
    align-items: initial;
    flex-direction: column;
    padding: 2.4rem 0;
  }
  .news .new__right {
    order: -1;
